/* Modal Styles - Custom Modal Structure */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.modal-overlay.open {
  opacity: 1;
  visibility: visible;
}

/* Modal Container */
.modal-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow: hidden;
  transform: scale(0.9) translateY(20px);
  transition: all 0.3s ease;
  position: relative;
  
}

.modal-overlay.open .modal-container {
  transform: scale(1) translateY(0);
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #E6D7A2;
  background: #D4AF5F;;
  color: #5F646D;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: #5F646D;
  order: 1;
  display: flex;
  align-items: center;
}

/* RTL Support for modal header */
.modal-header[dir="rtl"] {
  justify-content: flex-end;
}

.modal-header[dir="ltr"] {
  justify-content: flex-start;
}

/* RTL Support for modal title */
.modal-title[class*="text-end"] {
  text-align: right !important;
  margin-left: auto !important;
  margin-right: 0 !important;
  order: 2;
}

.modal-title[class*="text-start"] {
  text-align: left !important;
  margin-left: 0 !important;
  margin-right: auto !important;
  order: 1;
}

/* Force RTL layout for Arabic */
.modal-header[dir="rtl"] .modal-title {
  margin-left: auto !important;
  margin-right: 0 !important;
  text-align: right !important;
}

/* Force LTR layout for English */
.modal-header[dir="ltr"] .modal-title {
  margin-left: 0 !important;
  margin-right: auto !important;
  text-align: left !important;
}

.close-btn {
  background: none;
  border: none;
  color: #5F646D;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(95, 100, 109, 0.1);
  color: #5F646D;
}

/* Modal Body */
.modal-body {
  padding: 2rem;
  max-height: 70vh;
  overflow-y: auto;
}

.modal-footer {
  border-top: 1px solid #E6D7A2;
  padding: 1.5rem 2rem;
  gap: 1rem;
}

/* Form Styles */
.form-label {
  font-weight: 600;
  color: #5F646D;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
}

.form-control,
.form-select {
  border-radius: 8px;
  border: 2px solid #e9ecef;
  padding: 0.75rem 1rem;
  font-size: 0.875rem;
  transition: all 0.3s ease;
}

.form-control:focus,
.form-select:focus {
  border-color: #B68A35;
  box-shadow: 0 0 0 0.2rem rgba(212, 170, 58, 0.25);
}

.form-control.is-invalid,
.form-select.is-invalid {
  border-color: #dc3545;
}

.form-control.is-invalid:focus,
.form-select.is-invalid:focus {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  font-size: 0.8rem;
  color: #dc3545;
  margin-top: 0.25rem;
}

/* Button Styles */
.btn {
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.875rem;
  padding: 0.75rem 1.5rem;
  transition: all 0.2s ease;
  border: none;
}

.btn-primary {
  background-color: #B68A35 !important;
  color: #FFFFFF;
  border-color: #B68A35 !important;
}

.btn-primary:hover {
  background-color: #A87D2A;
  border-color: #A87D2A;
  color: #FFFFFF;
  transform: translateY(-1px);
}

.btn-primary:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background-color: transparent;
  color: #1B1D21;
  border: 1px solid #1B1D21 !important;
}

.btn-secondary:hover {
  background-color: #1B1D21;
  color: #FFFFFF;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-body {
    padding: 1.5rem;
  }
  
  .modal-footer {
    padding: 1rem 1.5rem;
    flex-direction: column;
  }
  
  .btn {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .modal-dialog {
    margin: 1rem;
  }
  
  .modal-body {
    padding: 1rem;
  }
  
  .modal-header {
    padding: 1rem;
  }
  
  .modal-title {
    font-size: 1.1rem;
  }
}

/* Contacts Section */
.contacts-section {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.contact-row {
  background: white;
  border-radius: 6px;
  padding: 1rem;
  margin-bottom: 0.75rem;
  border: 1px solid #e9ecef;
}

.contact-row:last-child {
  margin-bottom: 0;
}

.contact-actions {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
}

[dir="rtl"] .form-label {
  text-align: right;
}

[dir="rtl"] .btn-close {
  margin-left: 0;
  margin-right: auto;
}
